@import url('https://fonts.googleapis.com/css2?family=Nanum+Gothic:wght@400;700;800&display=swap');

.subRoot22 {
    height : 100%;
    color : white;
}

/* loading .. */
.load {
    position : relative;
    top : 50%;
    left : 50%;
    transform : translate(-50%, -50%);
    margin : 0;
    font-size : 3.125rem;
    text-align : center;
}

/* title */
.endTitle {
    margin : 0;
    font-size : 3.125rem;
}

/* end */
.endBox {
    position : relative;
    top : 50%;
    left : 50%;
    transform : translate(-50%, -50%);
    text-align : center;
}

.endBox p {
    margin : 0 0 50px 0;
    font-size : 2.0625rem;
}

.home-link {
    width : 130px;
    height : 40px;
    display : inline-block;
}

.home-link button {
    width : 100%;
    height : 100%;
    cursor : pointer;
    border : none;
    font-family: 'Nanum Gothic', sans-serif;
    font-weight : 600;
    font-size : 0.875rem;
    color : #4b4b4b;
    background : white;
    transition : 0.3s ease-out;
}

.home-link button:hover {
    color : #00e3a6;
    box-shadow : 0px 0px 18px 1px white;
    transition : 0.3s ease-out;
}