@import url('https://fonts.googleapis.com/css2?family=Nanum+Gothic:wght@400;700;800&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Dongle:wght@700&display=swap');

.writeBox textarea, .writeBox button, .btnYes, .btnNo {
    font-family: 'Nanum Gothic', sans-serif;
}

.subRoot11 {
    height : 100%;
}

/* modal*/
.modalBg {
    width : 100%;
    height : 100%;
    position : absolute;
    background : #000000bd;
    z-index : 999;
}

.modalBox {
    width : 350px;
    height : 200px;
    position : relative;
    top : 50%;
    left : 50%;
    transform : translate(-50%, -50%);
    display : inline-block;
    border-radius : 10px;
    background : white;
}

.modalCon {
    position : relative;
    top : 50%;
    left : 50%;
    transform : translate(-50%, -50%);
    padding : 15px 20px;
    text-align : center;
}

.m-que {
    margin : 0 0 15px 0;
    font-size : 1.6875rem;
    color : #00e3a6;
}

.m-con { 
    margin : 0 0 30px 0;
    font-size : 1.3125rem;
    color : #4b4b4b;
}

.m-link {
    margin-right : 10px;
    display : inline-block;
}

.m-link, .btnNo {
    width : 130px;
    height : 40px;
}

.btnNo {
    border : 1px solid #838383;
    background : white;
    font-size : 0.875rem;
    font-weight : 600;
    color : #838383;
    cursor : pointer;
    transition : 0.3s ease-out;
}

.btnNo:hover {
    color : white;
    background : #838383;
    transition : 0.3s ease-out;
}

.btnYes {
    width : 100%;
    height : 100%;
    border : none;
    background : #00e3a6;
    font-size : 0.875rem;
    font-weight : 600;
    color : white;
    cursor : pointer;
    transition : 0.3s ease-out;
}

.btnYes:hover {
    background : #07b889;
    transition : 0.3s ease-out;
}

/* white-space? 개행과 공백이 원래도록 보이게 처리 */
.writeStamp {
    white-space : pre;
}

/* title */
.writeBg {
    position : relative;
    top : 50%;
    left : 50%;
    transform : translate(-50%, -50%);
    z-index : 1;
}

.homeTitle {
    margin : 0;
    display : flex;
    justify-content : center;
    align-items : center;
    font-size : 3.125rem;
    color : #00e3a6;
}

.con {
    height : 45px;
    margin : 0;
    display : flex;
    justify-content : center;
    font-size : 1.5625rem;
    color : white;
}

/* writeBox */
.writeBox {
    display : flex;
    justify-content : center;
}

.writeBox form {
    width : 500px;
}

.writeBox textarea {
    width : 100%;
    height : 500px;
    margin-bottom : 10px;
    padding : 25px 30px;
    box-sizing : border-box;
    resize : none;
    border : none;
    font-size : 1rem;
    line-height : 30px;
    scrollbar-width : none;
}

.writeBox textarea:focus {
    outline : none;
}

.writeBox textarea::-webkit-scrollbar {
    display : none;
}

.writeBox button {
    width : 130px;
    height : 40px;
    float : right;
    cursor : pointer;
    border : none;
    color : #4b4b4b;
    font-size : 0.875rem;
    font-weight : 600;
    background : white;
    transition : 0.3s ease-out;
}

.writeBox button:hover {
    color : #00e3a6;
    box-shadow : 0px 0px 18px 1px white;
    transition : 0.3s ease-out;
}

@media (max-width : 600px) {
    .writeBox form {
        width : 85%;
    }
}